.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  border-color: aquamarine;
  border-width: 2px;
  border-style: solid;
}

.App-header-flexh {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  align-items: flex-start;
  justify-content:  flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
  border-color: rgb(242, 255, 127);
  border-width: 2px;
  border-style: solid;
  width:100%
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
